.Kull {
  position: relative;
  width: 550px;
  height: 335px;
  left: 200px;
  border: 1px solid #ccc;
  padding: 20px;

}

.Kull h5 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #777;
}

.Kull .btn {
  width: 40px;
  height: 40px;
  font-size: 20px;
  /* margin:10px; */
  border: 1px solid #ccc;
}

.Kull span {
  font-size: 18px;
  color: #222;
}

.Kull .count-btn {
  display: flex;
  /* justify-content: center; */
  align-items: center;

}

.price {
  position: absolute;
  right: 20px;
  bottom: 260px;
  color: #aaa;
}

.price strong {
  color: #333 !important;
}

.Kull .total {
  position: absolute;
  font-size: 18px;
  right: 20px;
  font-weight: bold;
  bottom: 100px;
}

.Kull .total strong {
  font-size: 32px;
}

.Kull .total span {
  font-size: 24px;
}

.cart {
  position: absolute;
  right: 10px;
  bottom: 0;
  width: 550px;
  height: 70px;
  font-size: 20px;
  background-color: #5F0080;
  color: #fff;

  margin: 10px;
  border: none;
  transition: all 0.3s ease-in-out;
  font-weight: bold;

}

.cart:hover {
  border: 2px solid #5F0080;
  color: #5F0080;
  background: #fff;
}